<template>
  <div class="contain">
    <div class="cover">
      <van-image :src="banner" contain />
      <!-- <div class="info">
        <span>宿 醉</span>
      </div> -->
    </div>

    <div class="info_box_con intro">
      <h3 class="title">剧本简介</h3>
      <p v-html="intro"></p>
    </div>

    <van-radio-group class="role" v-model="radio">
      <h3 class="title">剧本角色</h3>
      <div class="info_box_con item" v-for="obj in roleList" :key="obj.id">
        <van-image
          class="headImg"
          :src="obj.picUrl"
          @click.stop="ImagePre(obj.picUrl)"
        />
        <p v-html="obj.desc"></p>
        <van-radio
          :name="obj.name"
          icon-size="35px"
          checked-color="#ee0a24"
        ></van-radio>
      </div>
    </van-radio-group>

    <van-button class="ready" color="#ee0a24" round type="info" @click="start"
      >开始</van-button
    >
    <van-dialog v-model="show" title="标题" show-cancel-button> </van-dialog>
  </div>
</template>



<script>
import { ImagePreview } from 'vant';
import { Dialog } from "vant";
export default {
  props: ['banner', 'intro', 'roleList', 'chapter', 'name'],
  data () {
    return {
      radio: "",
      show: false,
    }
  },
  methods: {
    start () {
      if (this.radio) {
        console.log(this.chapter);
        // this.$store.commit("rolePick", this.radio);
        this.$router.push({
          name: this.name,
        })
        localStorage.setItem('chapter', JSON.stringify(this.chapter))
        localStorage.setItem('script', JSON.stringify(this.roleList[this.radio].script))
      } else {
        Dialog.alert({
          title: "你好歹选一个吧",
          message: "一个都不选你开啥本？",
        });
      }
      // Dialog.alert({
      //   title: "一个人开本？",
      //   message: "你是狗吧，还想提前看剧本？",
      // });
    },
    ImagePre (url) {
      ImagePreview([url]);
    }
  },
}
</script>

<style scoped lang="less">
.contain {
  padding: 20px 20px;
  background-color: #370b16;
  color: #c8baa8;

  .cover {
    margin-top: 20px;
    overflow: hidden;
  }
}

.intro {
  margin: 40px 0;
  padding: 40px 0;
  border: 1px solid #fff;
  text-align: center;

  .title {
    margin: 0;
    font-size: 35px;
    padding-bottom: 20px;
  }
  p {
    margin: 6px;
    white-space: pre-line;
    line-height: 50px;
  }
}
.role {
  .title {
    text-align: center;
  }
}
.info_box_con {
  background-color: #52292b;
  border-radius: 20px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  // padding: 20px 20px;
  text-align: center;
  position: relative;
  color: #c8baa8;
  font-size: 28px;
  letter-spacing: 2px;
}
.item {
  position: relative;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  // line-height: 230px;
  margin: 20px 0;
  overflow: hidden;
  .title {
    font-size: 30px;
  }
}
.title {
  font-size: 40px;
}
.headImg {
  width: 160px;
  position: absolute;
  height: 100%;
  left: 0px;
  top: 0;
}
.item p {
  text-align: center;
  line-height: 50px;
  margin: 0;
  padding-left: 160px;
  font-size: 35px;
}
.van-radio {
  position: absolute;
  right: 40px;
  bottom: 10px;
}
.ready {
  width: 100%;
  font-size: 40px;
  /* margin-left: 70%; */
  color: #52292b;
}
</style>