          script: [
            '你醒了过来，头有些晕沉沉的，整个人也感觉很疲惫，大腿上火辣辣的疼，低头一看，那里包着纱布，好像是受伤了。<br><br>你放眼看去，自己现在正处在一处病房里，天微微亮，你周围有五个人，分别是2男3女。<br><br>这些人看起来好像很眼熟的样子，对了，你能想起来，你们昨夜在一起喝酒来着，好像你们的关系还不错，但昨夜的记忆怎么一点都没有了呢?<br><br>不对,不光是昨夜,之前的记忆好像也乱七八糟的，你现在想起来你是一个专门给领导人搭配膳食的高级营养师。除此之外，你能想起来的只有一些零零散散的片段。<br><br>这时候医生来了，他对你们说:“你们没什么大碍，签个字就可以出院了。”<br><br>你可以回忆起以下的记忆片段<br><br>【公交车】你大喊一声:我是警察叔叔，不许动!<br><br>【奶奶】夜很黑，你感觉大腿好痛，一位慈祥的老奶奶领着你走向一处亮灯的地方，你隐约看到那地方挂着一块牌子“XXXX诊所”。老奶奶说:乖孩子，进去包扎一下吧。<br><br>【诊所】有个医生对着你没好气的说:治不了。<br><br>【便利店】那个看起来挺乖的文静女子说:刘克，那边有个便利店,你帮忙去买能止血的东西!快点!<br><br>【计程车】你问一个司机模样的人:这里最好的医院是哪个?<br><br>【打架】那个微胖男子(并没有鸡冠头）被几个人围着打，你上去拉开众人说:“这是我同学，要打也是我打，你们凭什么打?”<br><br>【地铁站】一个人指着一台机器说:去那边买去。<br><br>【地铁】一个地铁站的安全员拉住那个消瘦眼镜男，严厉的说道:也不看看你都多大的人了，和个小孩一样捣什么乱?<br><br>【医院】你来到一间医院，你感觉有点头晕，便问医生能不能给你看看。<br><br>【民宿】你冲着厨房喊叫:莫丽，萝卜切成丁哦!<br><br>【电线杆】那个文质彬彬的男子撕下电线杆上贴着的东西，扔在路边，说:“城市环境都是被这些乱贴牛皮癣的搞坏的。”<br><br>【餐馆】你拉着一个老头，恳求道:干爹，我悟性不低，回头一定来找您学艺!<br><br>【餐馆门口】你说:“子安，你坐下，我去买，兄弟我还不至于掏不起这几个钱!”<br><br>【马路边】一辆计程车停在马路边，司机小心翼翼的对你说:大哥，这单生意我不做，抱歉啊!<br><br>【小费】微胖男子(有了鸡冠头）拍了拍司机，笑了笑说:得，这500算小费，就当借你车用了1个半小时。<br><br>【忘了】你笑了笑说:忘了就忘了，别急，我有电话号码，我问问。<br><br>【美容院】文静女子对你悄悄的说:大鹏，我手机打不通，你快打110报警，别等她们炸了就晚了!<br><br>【我的货呢】几个凶神恶煞的人围着你们，不停的问:“我的货呢?东西呢?交出来!”你莫名其妙，不知道他们说的什么，你捏了捏拳头，想上去教训一下对方，但那个漂亮女子悄悄的拉了拉你。<br><br>【一起去 HIGH】有些宅的女子和文质彬彬的男子说了几句话，你好像听到她说“何子安……”',
            '在催眠师的帮助下，你终于回忆起了你的过往……<br><br>你今年29岁，名字叫做【段大鹏】，生活窘迫而糟糕，因为你并不是什么【高级营养师】,而是一个小地方的小餐馆【厨子】。<br><br>所谓的“高级营养师”，是你编造出来的谎言，目的是为了应付这次的高中同学会，因为同学们一个个都混的有模有样，只有你是最不如意的一个。<br><br>想当年，你们都是华美高中的学生，你从小生的人高马大，性格又极其急躁，动不动就喜欢用武力解决问题，所以你是一个学校里的小霸王，班里的同学都有些畏惧你。<br><br>高中时期，你们班有个男生叫做【何子安】，长的矮墩墩的，戴一副黑框眼镜，两个门牙特别突出，性格十分胆小内向。<br><br>何子安的体育成绩非常差，几乎没有什么运动能力，文科成绩中上，但数理化成绩极其拔尖，次次考试都几乎可以达到满分，老师都说何子安是未来的科学家，他和班里的同学【马晓光】和【刘克】关系比较好。<br><br>你最看不起光会学习的学生，恰好班里的同学们在讨论何子安的长相，说他像电视里放的《动物世界》里的一种动物，你一拍脑门，喊道:“何子安像河狸呀!”因为河狸是那种大门牙，胆子小的动物。<br><br>因为真的很形象，你起的这个外号很快就传开了。你心里也有些得意，谁说我段大鹏没有文采?起个绰号还不是张口就来?<br><br>你没事就欺负何子安一下，有时还会问他“借点钱花”，当然都是有借无还。即使他有马晓光和刘克这样的朋友你也不惧，他们3个加起来，还不够你一只手打呢!何况马晓光和刘克从来也不敢用正眼看你。<br><br>马晓光是一个不苟言笑的学生，听说父母是做生意的，他的身材和性格倒是和何子安有几分相似，但学习成绩可就差远了。<br><br>而刘克是一个差生，他非常喜欢打游戏，为此老师都找过他家长好几次了，不过听说刘克是一个电脑方面的高手，还会使用一些作图软件。<br><br>班里的学习委员叫【徐如雨】，她是一个要强的女生，文科成绩相当不错，但她的数理化成绩差何子安很多，导致每次考试总分都在何子安之下，所以能感觉出来她对何子安有一股敌意。<br><br>班花叫【莫丽】，几乎全班的男同学都对她表现出明显的好感，不过她只是别人眼中的班花，你对她并没有什么感觉，别人对莫丽献殷勤的时候，你甚至会冷笑几声来表示自己的不屑。<br><br>不过班里也有你钟意的女生，她的名字叫做【张萱萱】，是班里的文体委员。<br><br>张萱萱的家庭条件还不错，她是一个善良的女生，非常乐于助人，帮助过班里的很多同学。虽然她没有莫丽长得好看，但是她有一头乌黑发亮的长发静静垂在肩头，显得非常文静，这是你特别喜欢的类型，所以你经常会找机会和张萱萱聊天接触。<br><br>何子安最怕的事情就是上体育课，所以每当有户外活动的时候何子安都会尽量避免，为此，作为文体委员的张萱萱还专门批评过何子安。<br><br>不久后出了一个班级爆炸消息，听班里的同学说何子安给班花莫丽写了封信，莫丽当场就把那封信撕了。有好事的同学从垃圾堆里拼凑出了何子安的信,发现居然是一封情书，这下子何子安成了全班人眼中的笑料，有人还说他“癞河狸想吃天鹅肉”。<br><br>别看这何子安性格内向闷骚，但真还有点小聪明，有次老师在全班通报批评，说何子安谎称发烧来逃避上体育课，其实他是用袋子里的生石灰加水提高温度，然后把额头烤的烫烫的再让老师感受他的“发烧”。老师说这种小聪明不可取，当场就罚何子安在班里做了30个俯卧撑。<br><br>以何子安的身板，别说30个，就是3个都做不下来，所以那天几乎全班同学都看了他的笑话。你记得笑的最得意的就是学习委员徐如雨了。<br><br>第二天，徐如雨突然在班里大哭起来，还说要是谁损坏了爸爸的照片，她就和谁拼命。<br><br>过了几天，你看到何子安给徐如雨塞了什么东西，然后徐如雨扇了何子安一个耳光，怒声说有什么事你冲我来，别动我爸爸留给我的东西。<br><br>你喜欢运动，经常打篮球，水平也非常专业，经常一个人就能carry全场，很多同学都想和你学篮球，你看着顺眼的就会教一教。<br><br>有一天，你刚打完篮球，正在休息，何子安也吞吞吐吐的问你是否能教他打打篮球。<br><br>你轻蔑的一笑，当着很多同学的面说:“行啊!不过你得从我胯下钻过去!”<br><br>何子安脸上青红不定，大家也都纷纷起哄:“钻啊，钻啊!千载难逢的机会啊!”<br><br>何子安终究没有搭话，而是慢慢的转身走了。你觉得他没有答应就是削了你的面子，跑上去在他背上踹了一脚，他一个不稳摔在了一滩泥水里。然后就爬起来慢慢走了。<br><br>你虽然觉得自己似乎有点过分了，但还是大声骂了句:“这个死河狸，也不撒泡尿照照，还想学别人打篮球?”<br><br>不久后，班里和高年级举行篮球比赛，你带着几个班里同学和高年级同学比赛，在大比分落后的情况下你一个人连续进了好几个球，观战的同学都为你喝彩。<br><br>负责维护场上秩序的文体委员张萱萱替你买了冰镇的饮料，还拿手绢替你擦了汗让你加油。<br><br>心仪的女孩就在眼前，你豪情顿起，低声问张萱萱愿不愿意和你交往。<br><br>张萱萱脸上一红，用她细如蚊呐般的声音说:“大鹏，你要是能带着队伍赢了比赛，我就答应你……”<br><br>这么一来，你就像打了兴奋剂一样杀入球场，连连进球，最后在裁判吹响比赛结束的哨子之前，你居然拔地而起，像飞人一样跃起，几乎骑在了一个高年级对手肩上以灌篮的方式终结了对方的领先，最终以1分之差赢得了胜利。<br><br>之后，张萱萱也兑现了她的许诺，你们真的开始交往了。<br><br>那段时间是你这一生中最开心，最得意的日子，虽然你们除了轻轻拥抱和轻吻之外没有任何男女方面的深入接触，但你们经常在QQ上一聊就聊到深夜，聊未来，聊理想，甚至会聊到你们的终身大事……<br><br>张萱萱的学习成绩还不错，她会带着你复习学习，虽然你不是学习那块料，但在她的帮助下也有一定进步。<br><br>高三的时候你喜欢上了音乐，一直想要学习吉他，但你的家庭条件并不怎么好，家人还要供一个哥哥上大学，所以根本没有闲钱给你买吉他。<br><br>一个吉他可得几百块到几千块不等呢!你去了乐器城，在一家名为“徐徐好民谣”的吉他店里看上了一把吉他，价格是1300元。你偶尔问何子安敲诈的那点钱最多就是买点零食，差的太远了。<br><br>张萱萱家里条件不错，她父母也认为“女生要富养”，所以她手头零用钱很多。你不好意思以买吉他的借口来问她借钱，因为高三学习非常紧张，她一定会反对你这时候分心学吉他。<br><br>所以你对她撒了谎，你以哥哥生病为由问她借了很多次钱，每次都借50-100元不等。<br><br>这期间，你也经常去那家名为“徐徐好民谣”的吉他店，给老板说了你经济状况并不好，问他看看有没有更便宜，更适合你的吉他，一来二去，你和老板都混得比较熟了，他甚至知道了你的学校和班级。<br><br>这天，你在乐器城居然看到了你们班的学习委员徐如雨，她居然也在“徐徐好民谣”的店里，而且你还清楚的听到她叫老板“二叔”。<br><br>那天，你的心情很糟，你知道徐如雨和张萱萱关系不错，她二叔一定会把你经济不好还要攒钱买吉他的事情告诉徐如雨，而徐如雨也不会向张萱萱隐瞒此事。<br><br>就在你犹豫是否该向徐如雨求她不要说出这事之时，张萱萱给你发来了信息:“我以后再也不理你了，你这个骗子!”<br><br>你很羞愧，但还是发过去信息请张萱萱原谅，张萱萱发来一句“换做你是我，你会原谅你自己吗?”，然后就拉黑了你。<br><br>你很后悔，后悔自己为什么没有早一点和张萱萱坦白，而是用家人的健康来“绑架”张萱萱的爱心。虽然你不是一个好学生，但你的自尊心无人能比，你暗下决心，让时间来抚平张萱萱的怒气，这段时间你会好好学习，一定考一个好大学，到时候再向张萱萱求得原谅。<br><br>但所谓祸不单行，没几天你就被班主任叫了过去，班主任严厉的批评了你，说你早恋的事情她已经知道了，她还说张萱萱是很有希望考上名牌大学的，让你这个差生不要影响张萱萱的学习，而且老师已经通知了你的家长。<br><br>那天，你的整个世界都是灰暗的，你和张萱萱交往的事情你和张萱萱没有告诉任何人。那么老师只能是通过张萱萱自己才能知道这事，你没想到张萱萱为了摆脱你居然把你们交往的事情告诉了老师，难道你就这么让她憎恶吗?<br><br>你在教室待到放学，又待到天色渐晚，你想到回家就要面对老爸的一顿臭骂，所以也不想回去。<br><br>就在这时，你听到楼下的操场上似乎有人在说话，其中有个好像是何子安的声音。<br><br>你心情正烦躁，心想这个出气包“河狸”居然没有回家，那么正好让你出出气，于是你下了楼。<br><br>但是楼下的情景却很让你意外，何子安站在篮球场上，他被3个外校的痞子围着，一个痞子阴阳怪气在调侃着何子安，还有一个拿着篮球在何子安身上一下接一下的砸着。<br><br>你虽然平时也总欺负何子安，但他毕竟是班里的同学，你天生是一个非常护短的人。你可以欺负他，班里的同学也可以欺负他，但这些外校的痞子休想!<br><br>于是你冲过去推开了那个拿球砸何子安的痞子，冲突一下爆发起来，你们打在了一起。你一打三虽然不落下风，但对方毕竟人多，混战之中你的头被拍了一砖，你感到一阵眩晕就摔倒在地上，那三个痞子见状不妙连忙跑了。<br><br>何子安拿出毛巾替你擦拭头上渗出的血，还鼓励你:“大鹏!你撑住啊!我这就帮你叫急救车!”<br><br>突然间，你觉得何子安那带着大眼镜的胖脸居然如此的和蔼可亲，这小子，还挺够义气啊。<br><br>当天，何子安邀请你去他家吃饭，你刚好不想回家，又新交了朋友感到好奇和兴奋，便答应了他。<br><br>回去的路上，何子安告诉你他是因为个子矮才想学习打篮球，但平时又怕技术差丢人，上次想找你学篮球你又没答应。所以就想在放学后趁着没人自己练习，没想到有几个校外的痞子居然过来羞辱他。<br><br>你心里一阵惭愧，为你当时侮辱何子安的事情深感羞愧，如今是你补救的大好机会，于是你拍了拍胸脯，向何子安打包票说以后你就是他的篮球教练了!他高兴的向你连连感谢。<br><br>看到他兴奋的样子，你心里突然也感到了一丝久违的幸福感，原来帮助别人也是一件幸福的事情啊……<br><br>到了何子安的家里，你看到何子安的房间里居然有一把漂亮的吉他。<br><br>你问何子安能不能给你试试，何子安犹豫了一下便取了下来交给了你让你试试。<br><br>你试了试音，手感和音色都非常好，你自言自语的说:“我如果能有这么一把吉他就好了……”<br><br>何子安似乎看破了你的内心，突然大方的说:“大鹏，我们既然成了好朋友，这把吉他就当做礼物送给你了!”<br><br>他的话让你有些头晕目眩，但你看到墙上有很多何子安抱着吉他弹奏的照片，其中分明还有他抱着吉他拿着奖杯的照片，你知道这把吉他对何子安的意义很重要。<br><br>但你实在太想要这把吉他了，便说:“现在外边教人打篮球一节课也得好多钱，我一分钱也不收你的!”你的潜台词意思就是:你给我吉他，我教你打球。<br><br>何子安点了点头，笑了笑。<br><br>看到他答应，你重重的拍了一下何子安的肩膀:“子安，以后你就是我段大鹏的兄弟了!”<br><br>何子安腼腆的笑了笑，什么也没有说。<br><br>很快就迎来了高考，何子安发挥的比较正常，被保送上了国内有名的XM大学。而张萱萱发挥有些失常，最后上了Gz财经大学。<br><br>你则是考场失利，成绩连分数线都没到，父亲也知道你不是那块学习的料，于是就把你送去了烹饪学校学习。<br><br>你和何子安一直保持着联系。他偶尔会给你发一些他的自拍，如今的他越长越帅，个头也窜了很多，一下成了一个帅小伙，你由衷的为他感到高兴。<br><br>你从烹饪学校毕业后就做了厨子，工作很忙，但你也一直不忘练习弹吉他，你也在各种场合进行过锻炼和表演，虽然你弹的还不够精彩，但也偶尔会赢来零星的掌声。<br><br>何子安在厦门大学本硕博连读，学习非常忙，所以你们平时联络的也不多，2016年，何子安打来电话，告诉你说他很快就要博士毕业，准备留在学校当化学系的讲师。<br><br>你的工作则一直没有什么起色，一直都是一名普通的厨子。父亲多次说你也老大不小了，让你赶紧找对象。<br><br>同事介绍也好，相亲也好，你见过的姑娘不下10个了，但看中你的一个都没有，归根结底只有一个原因:穷。<br><br>2016年8月，何子安突然说他恋爱了，虽然你自己的对象还没着落，但仍为他感到高兴，便让他把对象照片发来，说你替他把把关。<br><br>何子安似乎有些不好意思，说你也认识这个女孩的。<br><br>你愣了一下，问他到底是谁。何子安犹豫了一下，终于说出了那个你许久都没有听到的名字:张萱萱。<br><br>顿时，你的心里有些五味杂陈，虽然有些醋意，但毕竟你和张萱萱的事情都是差不多10年前的往事了，何况何子安是自己的好兄弟，自己和张萱萱的感情早就没有了，怎么能吃兄弟的醋呢!<br><br>于是你大方的告诉他没关系，还故意撒谎说你早就找了女朋友，一直没告诉他是想给他一个惊喜。<br><br>何子安笑了笑，问你什么时候结婚，你只能搪塞着说看双方家长定时间。<br><br>何子安说张萱萱如今在做明星的私人化妆师，收入很可观。还问你谈女朋友快谈婚论嫁了吧，缺不缺钱?<br><br>你苦涩的想:“就是没钱才找不到对象，但是这种苦水何必倒给兄弟呢?他现在也正是需要钱的时候呀!”于是你假装豪气的说:“我现在事业也步入了飞升期,怎么会缺钱呢?兄弟你太小看我了!”<br><br>自从何子安和张萱萱交往后，你看过何子安在朋友圈里发过他和张萱萱很多亲密的合影，甚至有他们穿着内衣在床上拥抱亲吻的照片。<br><br>你觉得他发在朋友圈的照片尺度有些大了，你多次提醒过何子安注意照片的尺度，但何子安哈哈一笑，他说你太落伍了，现在年轻人秀恩爱都这么高调露骨的。更大尺度的有的是，只不过他不好意思发出来而已。<br><br>你听了后心里有些不是滋味，心想本来张萱萱应该在你的怀里缠绵，但现在却整天和你最好的兄弟温存，但你看到现在混的这个样子，也知道张萱萱选择何子安要比选择自己强100倍。<br><br>2019年8月多,何子安突然告诉你他要和张萱萱订婚了,想请你和一些老同学来聚一聚。<br><br>你大概打听了一下，