            '你醒了过来，头有些晕沉沉的，心里似乎有一种复仇的快感。<br><br>你放眼看去，自己现在正处在一处病房里，天微微亮，你周围有五个人，分别是3男2女。<br><br>这些人看起来好像很眼熟的样子，对了，你能想起来，你们昨夜在一起喝酒来着，好像你们的关系还不错，但昨夜的记忆怎么一点都没有了呢?<br><br>不对,不光是昨夜,之前的记忆好像也乱七八糟的，你现在想起来你是一个全国闻名的酒吧特约歌手。除此之外,你能想起来的只有一些零零散散的片段。<br><br>这时候医生来了，他对你们说:“你们没什么大碍，签个字就可以出院了。”<br><br>你可以回忆起以下的记忆片段<br><br>【公交车】你在一辆公交车上，看到一个小偷正在把手伸向一个乘客的背包，一个壮硕男子突然站出来大喊了一声。<br><br>【奶奶】夜很黑，前边好像有个亮灯的诊所，那个壮硕男子感激的对一个老奶奶说:奶奶，真的太感谢你了，你以后就是我的奶奶。<br><br>【诊所】这里看起来好像怪怪的，医生像看神经病一样看着你们。<br><br>【便利店】你看向便利店方向，一个蒙着头的蒙面人从里边冲了出来。<br><br>【计程车】计程车司机指着一个方向对壮硕男子说:看见没，地铁站，坐两站路就是蓝海医院，我们这最大的医院。不过老兄你这病也未必能治啊!<br><br>【打架】那个微胖男子（并没有鸡冠头）嚷嚷着:我怎么就不是童子了?<br><br>【地铁站】你不满意的嘟囔着:都9012年了，还不能电子支付啊?<br><br>【地铁】你白了那个消瘦眼镜男一眼，心想:这家伙是有妄想症吧。<br><br>【医院】一个医生嘱咐说:回去记得给ta多喝点排骨汤补补。<br><br>【民宿】你掇了搬嘴，说:真讨厌，东西都备齐了，怎么没有气了!看来只能出去了。<br><br>【电线杆】你也看了看说:看起来像个正常人啊，怎么就走丢了?<br><br>【餐馆】你低声对那个看起来有点宅的女子说:如雨，打开你包看看。你看那边坐着的那个人像不像刚才那个?<br><br>【餐馆门口】一个看起来有些面熟的凶恶男子被消瘦眼镜男、壮硕男子、微胖男子（并没有鸡冠头)压在餐馆门口地上，他手一甩，不停的叫骂:放开我，否则我叫兄弟杀你全家!<br><br>【马路边】你对司机说:你怎么这么没有爱心，人家的家人多着急啊，电话又打不通。<br><br>【小费】微胖男子(有了鸡冠头）拍了拍司机，笑了笑说:得，这500算小费，就当借你车用了1个半小时。<br><br>【手表】你抬了抬手,皱着眉说:这表怎么不走呢?一点也不好看,带着轻飘飘的,去典当行当了算了。<br><br>【美容院】你指着一间已经打烂的店铺骂道:我最恨美容院了，我今天要炸了它!你们都别拦着我!<br><br>【我的货呢】几个凶神恶煞的人围着你们，不停的问:“我的货呢?东西呢?交出来!”你注意到其中有个人腰里居然别着一把枪,壮硕男子捏了捏拳头似乎想出头，你连忙拉住了他。<br><br>【一起去 HIGH】那个文静女子说:“莫丽，如雨，我不要和你们分开!”',
            '在催眠师的帮助下，你终于回忆起了你的过往……<br><br>你今年28岁，名字叫做【莫丽】，生活过得很平凡，因为你并不是什么【ladyface酒吧特约驻唱歌手】，而是一个【没什么人气的网络小主播】。<br><br>所谓的“特约驻唱歌手”，是你编造出来的谎言，目的是为了应付这次的高中同学会，因为同学们一个个都混的有模有样，只有你是最不如意的一个。<br><br>想当年，你们都是华美高中的学生。<br><br>你从小就长得很出众，初中时期就已经傲视全班。到了高中，身材玲珑浮凸的你更是当仁不让摘取了班花的无冕之冠。<br><br>除了你的眼皮是单眼皮，眼睛略显小之外，其他的一切都很完美。<br><br>几乎全班的男生都对你常常献殷勤，但你眼界很高，根本看不上这些只会低声下气的讨好你的男生，你始终相信，太容易得到的没有好东西。<br><br>你的学习成绩平平，但这样也好，省得你又美又是学霸，那样就成了全班女生的公敌了。<br><br>你在高中时期有个好朋友【徐如雨】，她是班里的学习委员，各科成绩都不错。每次考试都名列前茅，老师非常喜欢她。<br><br>班里还有个叫【张萱萱】的，她是文体委员，她有一头乌黑发亮的长发静静垂在肩头，显得非常文静。不过论起颜值，你有自信碾压她。<br><br>你们班有个男生叫做【何子安】，长的矮墩墩的，戴一副黑框眼镜，两个门牙特别突出，性格十分胆小内向。班里长得高高大大的“小霸王”【段大鹏】还给何子安取了一个外号叫“河狸”，因为河狸是那种大门牙，胆子小的动物，这个何子安给人的感觉倒真的有些像河狸呢!<br><br>何子安的体育成绩非常差，几乎没有什么运动能力，文科成绩中上，但数理化成绩极其拔尖，次次考试都几乎可以达到满分，老师都说何子安是未来的科学家，他和班里的同学【马晓光】和【刘克】关系比较好。<br><br>马晓光是一个不苟言笑的学生，听说父母是做生意的，他的身材和性格倒是和何子安有几分相似，但学习成绩可就差远了。<br><br>而刘克是一个差生，他非常喜欢打游戏，为此老师都找过他家长好几次了，不过听说刘克是一个电脑方面的高手，还会使用一些作图软件。<br><br>实话说，高中时期的男生你都忘的差不多了，但为什么你会记着这几个男生，其实也是有原因的。<br><br>因为，马晓光刘克从未主动找你说过话，而段大鹏看到你也是一脸的不屑。<br><br>你不明白，自己如同众星捧月一般，他们3个人居然能抵得住你绝世容颜的诱惑?<br><br>至于何子安，你能记住他是因为他实在太特殊了。<br><br>和很多男生一样，何子安也表现出对你的好感，他还找马晓光给你送了一个破音乐盒，还拜托马晓光来向你表白。<br><br>你暗骂一声傻瓜，心想这家伙也不照照镜子就敢来追求你，你不介意他出更大的丑，便回复他:“喜欢我就自己来表白呀!”<br><br>结果这个何子安在你的“鼓动”下，成了全班第一个给你公然写情书的男生。只不过你当场撕掉了他的情书，还告诉他:“何子安，我现在告诉你，只有做了才知道我会拒绝你!”<br><br>这么一来，你让何子安至少承包了同学们2个月的笑点（嘲笑)。有人还说他“癞河狸想吃天鹅肉”。<br><br>徐如雨是一个要强的女生，文科成绩相当不错，但她的数理化成绩差何子安很多，导致每次考试总分都在何子安之下，所以能感觉出来她对何子安有一股敌意。<br><br>何子安最怕的事情就是上体育课，所以每当有户外活动的时候何子安都会尽量避免，为此，作为文体委员的张萱萱还专门批评过何子安。<br><br>别看这何子安性格内向闷骚，但真还有点小聪明，有次老师在全班通报批评，说何子安谎称发烧来逃避上体育课，其实他是用袋子里的生石灰加水提高温度，然后把额头烤的烫烫的再让老师感受他的“发烧”。老师说这种小聪明不可取，当场就罚何子安在班里做了30个俯卧撑。<br><br>以何子安的身板，别说30个，就是3个都做不下来，所以几乎全班同学那天都看了他的笑话。你记得笑的最得意的就是学习委员徐如雨了。<br><br>第二天，徐如雨突然在班里大哭起来，还说要是谁损坏了爸爸的照片，她就和谁拼命。<br><br>过了几天，你看到何子安给徐如雨塞了什么东西，然后徐如雨扇了何子安一个耳光，怒声说有什么事你冲我来，别动我爸爸留给我的东西。<br><br>经过你的询问，才得知是徐如雨的父母离婚了，她的父亲以前对她非常好，现在已经亡故了,她妈妈把所有她父亲的照片都销毁了,她放在文具盒里的是最后一张父亲的照片。是何子安玩恶作剧将徐如雨父亲的照片藏起来了，过了好几天才还给了徐如雨。<br><br>你劝徐如雨反正他已经还给你了，你也教训过他了，就别再生气了，徐如雨才渐渐消了气。<br><br>年级里时时会举办一些体育赛事，篮球赛就是最常见的一项。<br><br>因为张萱萱是文体委员，所以会经常负责这样的赛事场上的秩序和比分统计等工作。<br><br>有次，你们班要和高年级同学打一场篮球比赛，队里的主力是段大鹏，在大比分落后的情况下段大鹏一个人连续进了好几个球，观战的同学都为他喝彩。<br><br>你觉得段大鹏好帅，好想上去和他合影，但女生的矜持让你没有这么做。<br><br>在中场休息时，张萱萱替段大鹏买了冰镇的饮料，还拿手绢替他擦了汗。<br><br>下半场比赛，段大鹏连连进球，最后在裁判吹响比赛结束的哨子之前，他居然拔地而起，像飞人一样跃起，几乎骑在了一个高年级对手肩上以灌篮的方式终结了对方的领先，最终以1分之差赢得了胜利。<br><br>你心头如同小鹿乱撞，这个男生太帅了，整个班都在为他欢呼。不过你注意到段大鹏始终没有多看你一眼，你叹了口气，心想，心仪的东西都是不容易得到的……<br><br>从那场篮球赛之后，你注意到张萱萱和段大鹏处的很近，有人说他们在秘密交往，你心里颇不是滋味。<br><br>不久后的一天，你在体育课上从双杠上掉了下来，当时就昏迷了过去。<br><br>醒来后，已经是第二天下午了，你已经躺在了医院病床上，医生说是脑震荡，小腿也骨折了。<br><br>你苏醒后，很多同学都来看你，房间里堆满了礼品，而何子安只送了你一只纸鹤，还非得让你拆开。<br><br>你心想这个何子安真是抠门，折个纸鹤分分钟的事情，居然拿这个送礼?你当下就把那个纸鹤扔进了垃圾桶。<br><br>你的伤势恢复的很快，只用了一个多月就能下地行走了,医生都说年轻人体质就是好啊。<br><br>到了高三下半学期的一天，徐如雨神神秘秘的告诉你，说段大鹏其实一直喜欢的是你，只不过他一直不好意思告白。<br><br>你不太相信，但徐如雨说她从不撒谎，这个消息绝对准确。<br><br>你心想段大鹏一直都不怎么理你，原来他不是不喜欢你，而是在“欲擒故纵”啊!<br><br>你想马上就要高考了，既然他不好意思，你就向他告白吧!于是你写了一张字条，签了自己的署名，在课间放在了段大鹏的书包里。<br><br>字条上写的是:大鹏，其实我早就喜欢你了，咱们班的那些男生只会低声下气的讨好我，只有你很特殊，你打篮球时的英姿一直在我脑海里晃动。马上就要高考了，很快同学们就会分道扬镳，我赶在高考前想对你说一声:我喜欢你，愿意做你的女朋友，不管你喜不喜欢我，不管你是不是对我不屑一顾，我都不会后悔今天向你做出的表白。<br><br>下午的时候，徐如雨作为学习委员去老师那里拿作业，她说刚好听到老师在给段大鹏家长打电话，说的是段大鹏早恋的事情。<br><br>你吓的直吐舌头，很后悔写了那张字条，很怕老师处分你。<br><br>但这事情渐渐的过去了，到了高考也没人再提起过。<br><br>很快就迎来了高考，你考的很普通，上了个Gz财经大学。<br><br>徐如雨发挥的还不错，上了她心仪的大学。<br><br>何子安很厉害，被保送上了国内有名的XM大学。<br><br>而段大鹏连分数线都没过，最后好像去了什么烹饪学校。<br><br>上了大学后，你继续保持了你的班花头衔，学校里很多学长学弟都是你的追求者，你渐渐就忘了那个连大学都没考上的段大鹏。<br><br>这里的男生都娘里娘气的，没有你喜欢的那种看起来很有男子气概的男生，所以你并没有打算谈恋爱，而是把精力放在了学习上。<br><br>你们学校门口有个梦飞翔网吧，你有时候会去那里修改一些论文什么的。<br><br>有次，你从梦飞翔网吧回学校的路上居然遇到了你的高中同学刘克。<br><br>他乡遇同学，你很兴奋，和刘克聊了很久，才知道刘克和你考上了同一所大学。<br><br>刘克提议要请你吃饭，但他不算是你喜欢的类型，你不想欠他什么情，便笑着拒绝了。<br><br>之后，你和刘克又见了几次，刘克居然还拿出一个包装好的IPHONE手机送给了你，你早就想买个新手机了，但生活费一直挺紧张的，没想到刘克一送就送你一个这么好的手机，你对他的印象顿时大为改观。<br><br>过了段时间，刘克经常会送你一些小礼物，有名牌香水口红化妆品什么的，你知道刘克想追你，但你觉得他长得并不帅，而且也没有男子汉气概，所以也一直在犹豫。<br><br>有天晚上，你下晚自习后路过学校内的小树林，里边突然走出两个浑身酒气的流氓，他们拦住你对你百般调戏，说着一些很露骨的话，还尝试对你动手动脚。<br><br>你吓的魂不附体，就在你不知道该如何是好的时候，一个黑影突然冲了过来，疯了一样的和那两个流氓打了起来，最终那⒉个流氓被打跑了，你定睛一看，才发现这个救了你的人居然是刘克。<br><br>刘克说他听说最近老有流氓在大学校园里骚扰女生，他不放心你下晚自习一个人走，所以每天都在这里等你，看你安全回到