          script: [
            '你醒了过来,头有些晕沉沉的,额头上有个小伤口，不过已经不太痛了，另外，你的发型出奇的古怪，居然是一个朋克造型的鸡冠头。<br><br>你放眼看去，自己现在正处在一处病房里，天微微亮，你周围有五个人，分别是2男3女。<br><br>这些人好像很眼熟的样子，对了，你能想起来，你们昨夜在一起喝酒来着，好像你们的关系还不错，但昨夜的记忆怎么一点都没有了呢?<br><br>不对,不光是昨夜,之前的记忆好像也乱七八糟的，你现在想起来你好像是一个富二代，刚刚继承了家族的产业。除此之外，你能想起来的只有一些零零散散的片段。<br><br>这时候医生来了，他对你们说:“你们没什么大碍，签个字就可以出院了。”<br><br>你可以回忆起以下的记忆片段<br><br>【公交车】你在一辆公交车上，看到一个小偷正在把手伸向一个乘客的背包，那个壮硕男子突然站出来大喊了一声。<br><br>【奶奶】夜很黑，前边好像有个亮灯的诊所，那个壮硕男子感激的对一个老奶奶说:奶奶，真的太感谢你了，你以后就是我的奶奶。<br><br>【诊所】一头野狼不知道从哪里冲了出来，你顺手抄起旁边的一个凳子就抡了过去。<br><br>【便利店】那个消瘦眼镜男嘟囔着:我一个大男人提着那东西怎么好意思啊?<br><br>【计程车】那个壮硕男子挥手喊道:“计程车!”<br><br>【打架】你一摸头，见血了，你慌了神，嚷嚷起来:我快死了!<br><br>【地铁站】一个文质彬彬的男人说:“我们两个身上有一卡通，不用给我们买。”<br><br>【地铁】你忍住笑，低声对那个消瘦眼镜男说:你是不是武侠小说看多了?<br><br>【医院】你来到一间医院，一个医生对你皱了皱眉，说道:都这么大的人了，胡闹什么?回去贴张创可贴就行了。<br><br>【民宿】那个漂亮女子(手腕上并没有画表）白了你一眼，说:你怎么这么没眼色，没看见我在这切菜吗?去去去，找别人去。<br><br>【电线杆】你看了一眼，说:可惜……咋不是富婆重金求子的呢?<br><br>【餐馆】你递给消瘦眼镜男一样东西，说:这玩意名字里好歹有个龙字，带在身上驱驱邪。<br><br>【餐馆门口】文质彬彬男子说:“我去买单，世界上长得像的人多了，你们别惹事。”<br><br>【马路边】你对司机说:你怎么这么没有爱心，人家的家人多着急啊，电话又打不通。<br><br>【小费】微胖男子（有了鸡冠头）拍了拍司机，笑了笑说:得，这500算小费，就当借你车用了1个半小时。<br><br>【手表】你抬了抬手,皱着眉说:这表怎么不走呢?一点也不好看,带着轻飘飘的,去典当行当了算了。<br><br>【美容院】你指着一间已经打烂的店铺骂道:我最恨美容院了，我今天要炸了它!你们都别拦着我!<br><br>【我的货呢】几个凶神恶煞的人围着你们，不停的问:“我的货呢?东西呢?交出来!”你注意到其中有个人腰里居然别着一把枪,壮硕男子捏了捏拳头似乎想出头，你连忙拉住了他。<br><br>【一起去HIGH】那个文静女子说:“莫丽，如雨，我不要和你们分开!”',
            '在催眠师的帮助下，你终于回忆起了你的过往……<br><br>你今年29岁，名字叫做【马晓光】，生活窘迫而糟糕，因为你并不是什么【家族企业继承人】，而是一个【代驾司机】。<br><br>所谓的“家族企业继承人”，是你编造出来的谎言，目的是为了应付这次的高中同学会，因为同学们一个个都混的有模有样，只有你是最不如意的一个。<br><br>想当年，你们都是华美高中的学生。<br><br>你父母是做食品小生意的，日子还过得去。<br><br>你从小不是很聪明，学习成绩平平，身材也不高，还有点胖，而且还非常懒。懒得学习，懒得看书，甚至都懒得玩。<br><br>高中时期，你们班有个男生叫做【何子安】，长的矮墩墩的，戴一副黑框眼镜，两个门牙特别突出，性格十分胆小内向。<br><br>何子安的座位就在你的后边，一来二去，你们渐渐熟悉了，成了一对好朋友。<br><br>班里还有一个贪玩的男生叫【刘克】，刘克学习成绩不好，经常逃课去玩电脑，为人倒很健谈，和你认识没几天后就拉着你去网吧和他一起打游戏。<br><br>但你觉得那些游戏也没什么意思，还不如回家睡觉，所以和刘克去玩了几次之后就基本不再去网吧了。<br><br>反倒是何子安，他通过你认识了刘克，经常会和刘克去一起打游戏。<br><br>不过何子安很有自制力，他从不逃课，只有放学和周末才会去玩游戏。何子安的体育成绩非常差，几乎没有什么运动能力，文科成绩中上，但数理化成绩极其拔尖，次次考试都几乎可以达到满分，连老师都说何子安是未来的科学家。<br><br>你们班里有个叫【段大鹏】的，生的人高马大，他是学校里的霸王，经常欺负班里的同学，尤其喜欢欺负何子安，还给何子安取了一个外号叫“河狸”。很快这个外号就在班里叫了起来。<br><br>何子安偷偷的告诉你，说这个段大鹏经常会敲诈他零花钱，你也有些怕段大鹏，所以只能安慰何子安最好少招惹他，惹不起咱们就躲。<br><br>班里的学习委员叫【徐如雨】，她是你的同桌，也是一个要强的女生，文科成绩相当不错，但她的数理化成绩差何子安很多，导致每次考试总分都在何子安之下，所以能感觉出来她对何子安有一股敌意。<br><br>班花叫【莫丽】，几乎全班的男同学都对她表现出明显的好感，你也不例外，但你这样又矮又挫，学习差，性格懒的人她是不会喜欢的，所以你也懒得去追求她。<br><br>班里还有一个叫【张萱萱】的女生，她是文体委员，也是一个乐于助人的善良女生。有一头乌黑发亮的长发静静垂在肩头，显得非常文静。段大鹏好像很喜欢张萱萱，经常都会出神的盯着张萱萱看。<br><br>你的同桌徐如雨文科很优秀，她有时候还会作一些诗歌，你觉得她超有才华，很佩服她。<br><br>你家里是做食品小生意的,知道哪些零食比较好吃,所以你经常给徐如雨带好吃的零食，徐如雨客气了几次也就来者不拒了。<br><br>这个徐如雨，不光会写诗，而且吃东西的样子偷偷摸摸的，超可爱，你一下子就被她迷住了。<br><br>有时候老师会发现你们吃零食，但你总是抢着将错误揽在自己身上。你能感觉到徐如雨对你越来越信任了。<br><br>不久后的一天，你鼓起勇气告诉徐如雨说你有些喜欢她，但没想到，她白了你一眼，说:“学生以学业为重，你别胡思乱想，否则我就告诉老师!”<br><br>那几天你心情很不好，徐如雨的拒绝给你了很大的打击。<br><br>何子安最怕的事情就是上体育课，所以每当有户外活动的时候何子安都会尽量避免，为此，作为文体委员的张萱萱还专门批评过何子安。<br><br>何子安虽然很内向，但他也喜欢漂亮的异性，他偷偷告诉你，班花莫丽就是他的女神。你觉得很好笑，莫丽那种高冷女神追的人数都数不过来，何子安算老几。<br><br>连徐如雨这种长相普通的女生都看不上你，何子安和你只是半斤八两，莫丽更加看不上他。<br><br>你刚刚被徐如雨拒绝，感觉出了丑，你也想看何子安出一回丑，便故意给何子安说你听说莫丽对学习好的学生特别佩服，尤其是数理化学的好的莫丽尤其佩服。<br><br>你还说莫丽特别喜欢大方的男孩，还说自己愿意替何子安探探口风，让何子安准备点礼物。<br><br>何子安信了你的话，非常兴奋，但他不懂给女孩买什么礼物，让你帮他选，还让你替他问问莫丽的口风。<br><br>你便问何子安要了500元钱，买了一个音乐盒，只花了39元，剩下的钱你也没有还给何子安，而是自己留着花了。<br><br>你把那个便宜货音乐盒送给莫丽，就说是何子安喜欢她，托你送的音乐盒。<br><br>莫丽冷笑了一下，没说什么话。<br><br>不久后听班里的同学说他给班花莫丽写了封信，莫丽当场就把那封信撕了。有好事的同学从垃圾堆里拼凑出了何子安的信，发现居然是一封情书，这下子何子安成了全班人眼中的笑料，有人还说他“癞河狸想吃天鹅肉”。<br><br>别看这何子安性格内向闷骚，但真还有点小聪明，有次老师在全班通报批评，说何子安谎称发烧来逃避上体育课，其实他是用袋子里的生石灰加水提高温度，然后把额头烤的烫烫的再让老师感受他的“发烧”。老师说这种小聪明不可取，当场就罚何子安在班里做了30个俯卧撑。<br><br>以何子安的身板，别说30个，就是3个都做不下来，所以几乎全班同学那天都看了他的笑话。那天，徐如雨看到何子安出丑，笑的非常开心得意，还低声骂了一句“死胖子，就你那么点花招还想瞒天过海。”<br><br>因为何子安数理化成绩特别好，你有时候会去何子安家向他请教一些问题。<br><br>他是单亲家庭，只有父亲没有母亲，但家庭条件很不错，房子装修的很豪华。而且他还有一把漂亮的吉他，墙上还有何子安抱着吉他拿着奖杯的照片。原来何子安还是个吉他高手呀。<br><br>何子安拿下吉他弹奏了2曲，你觉得很有意思，想问他借玩几天。<br><br>但何子安严肃的说这个吉他是他的宝贝，连他的爸爸都不能摸，所以肯定不会借给你。<br><br>你有些失望，心想我的东西都可以借给你，一把破吉他还这么抠门。所以你生气的好几天没有和何子安说话。<br><br>你平时注意到徐如雨经常出神的看着她文具盒内部的一张照片，那是一个极为帅气的青年男子。<br><br>怪不得徐如雨不喜欢你，原来在文具盒里偷偷藏了一个大帅哥，你心里又妒又恨。<br><br>于是你趁有次课间徐如雨不在，恶作剧偷偷的拿走了她文具盒里的照片，然后扯碎后扔到了何子安的书包里。哼，谁让这个何子安连个破吉他都不给你借。徐如雨要怀疑就怀疑何子安吧!<br><br>不久后，徐如雨回来了，她发现照片不见了，急的她到处找，找不到就大哭了起来，还说要是谁损坏了爸爸的照片，她就和谁拼命。<br><br>你看到她那闪着强烈恨意的双眼，不禁打了个哆嗦。那居然是她爸爸的照片……<br><br>你不敢告诉她这个事是你干的，她似乎也没有怀疑你。<br><br>过了大概1个多星期，有天下课的时候，你看到徐如雨扇了何子安一个耳光，怒声说有什么事你冲我来，别动我爸爸留给我的东西。而何子安则说了声对不起。<br><br>这时候你心里有些愧疚,心想肯定是那张照片的事情,你本想去给徐如雨说是你拿走的，但心想何子安都已经替你挨过打了。所以你最终没有提起此事。<br><br>很快，到了高三，那段时间张萱萱和段大鹏处的很近，你在课间的时候甚至见过他们手拉手在一起亲密聊天。<br><br>但突然有一天，张萱萱就和段大鹏互相不理睬对方了，也不知道是为什么。<br><br>又过了几天，有天课间刘克突然神神秘秘的拿出一张信纸给你和何子安看，上边的内容居然一个女生写的表白信。而信的署名居然是班花莫丽……<br><br>真是爆炸性新闻，高冷的班花莫丽居然喜欢上了这个只会打游戏的穷小子刘克?而且莫丽在信中还说“你****的英姿一直在我脑海里晃动”，“****”看样子是四个涂掉的字。这个刘克又有过什么英姿了?难不成他打游戏的样子很帅?<br><br>你没想到这个刘克这么厉害，不吭不哈的就得到了班花的青睐。<br><br>2008年，毕业了，大家进行了高考。<br><br>你考的一般，上了一个普通的本地二本。<br><br>何子安考的很好，被保送上了国内有名的XM大学。<br><br>刘克超常发挥，居然超了分数线好几十分，成功的被GZ财经大学录取。<br><br>至于那个段大鹏，据说连分数线都没考到，回去乖乖种田吧!<br><br>而你喜欢的徐如雨发挥的也不错，考上了她心仪的大学。<br><br>上了大学后，你仍是一副懒洋洋睡不醒的状态。<br><br>你和何子安经常还有联系，他偶尔会给你发一些他的自拍，如今的他越长越帅，个头也窜了很多，一下成了一个帅小伙。<br><br>你看着不禁有些妒意，原本你和他都是班里的“地雷”，如今他旧貌换新颜，你还是外甥打灯笼“照舅（旧)”。<br><br>稀里糊涂混了几年，你就毕了业。<br><br>毕业后，你进了一家化妆品公司（盛颜公司）做销售，可能因为你模样胖胖的，说话声音带点磁性，很招人信赖，所以业绩一直不错。<br><br>不久后，公司扩大规模招了一批新人，你居然看到徐如雨也在这批新招的员工当中。<br><br>徐如雨看到你也很惊讶，你们都在感叹世界太小了。<br><br>和徐如雨成了同事后，你想现在大家都是成年人了，她可没有理由拒绝你的追求了，于是你经常会约徐如雨一起吃饭。<br><br>徐如雨对销售这行不是很在行，所以业绩平平，公司月会时经理狠狠的批评了她，还放出狠话，让她下个月不提升业绩就不要来了。<br><br>你看到徐如雨无助的样子，一股想要保护她的勇气油然升起，当下就掀了桌子。<br><br>掀了桌子后，经理很吃惊的看着你，他没想到平时话都不多的你会表现的这么张狂。此时的你也骑虎难下了，就豁出去不干的觉悟，大声批评着领导，故意说这个盛颜公司都是自己家投资的产业，自己来这里只是为了锻炼，还让经理快点给徐如雨道歉，否则自己会动用自己的关系开除经理。<br><br>经理的脸上青红不定，他真的被你唬住了，当即就给徐如雨道了歉。<br><br>当天下班，你邀请徐如雨一起去看电影，她同意了，在电影结束的时候，你向徐如雨表白，但她给你发了好人卡，最终拒绝了你。<br><br>徐如雨第二天就递交了辞职报告，说她自己不是干销售的料，说你是个好人，让你不用为她的事费心。<br><br>你想留她，但你知道你唬住经理的事情很快就会真相大白，与其到时候丢人，不如让她带着对你的好印象离开吧。<br><br>徐如雨离职后没几天，你也识趣的辞了职。<br><br>你辞职后，又找了2份工作，不过都没干长久。<br><br>2013年，你通过前同事介绍，了解了一种叫3M金融互助的平台，这个平台是一个很出名的俄罗斯人马夫罗季办的，据同事说他的很多亲戚都靠这个赚了大钱。<br><br>你很感兴趣，将自己的积蓄都投了进去，不到2个月就赚了2万。<br><br>你很想多投点，但你之前积蓄实在不多，所以你想到了家境不错的何子安。<br><br>你联系了何子安，想问他借10万元，但何子安说自己手头很紧，最终只借给了你5000。你后来陆陆续续又借了几千元，还把这个平台能发财的事情告诉了何子安，让他相信你。<br><br>但何子安回复你说那个根本就是庞氏骗局，还让你现在尝点甜头就赶紧退出吧，他是不会再借钱给你了，何况他也没钱。<br><br>你一气之下就再也不联系何子安了。<br><br>你把自己赚的钱再投资，很快你的钱就滚到了10万元。你这时候发现发财原来如此简单，每天只用坐在家里看看小说钱就自己进口袋来了。<br><br>说起看小说，你当时在阅读网站迷上了一本叫做《残照又当楼》的架空历史小说，作者叫“雨慢漫”，看性别是个女孩。<br><br>小说的文风非常对你的胃口，于是你化名“创世一哥”，通过网站的打赏系统给雨慢漫打赏了好几次价值几千元的大礼包。<br><br>雨慢漫的小说在网站人气其实并不高，只有2个忠实读者，你是其中的一个，还有一个读者叫宅男好可乐。你们两个也经常会聊起雨慢漫的小说，你发现这个宅男好可乐还和你挺聊得来的。<br><br>你越来越喜欢雨慢漫，甚至还在网站私信她，告诉她你特别喜欢她的小说，还想追求她。<br><br>看到你的告白她也十分惊喜，表示愿意和你交往。<br><br>于是你们在网络上你侬我侬，感情迅速升温。通过了解，你得知雨慢漫大学毕业后放弃了薪酬不菲的工作，而是选择追求自己的梦想——写小说。你十分欣赏她，还告诉她你是一名金融投资商，月薪10万。<br><br>过了几天，你家里停电，你跑到附近的一所新开的网吧上网，意外的发现网吧营业执照上的法人居然是何子安，你心生愤恨，心想这家伙枉称兄弟，有钱开网吧也不愿意借给你投资，太黑了。<br><br>你把这件事告诉了宅男好可乐，他替你出谋划策，怂恿你烧了这个枉自称兄弟的家伙的网吧。<br><br>说干就干，于是你便在网上购买了不少酒精。半夜悄悄的在网吧后门倒了酒精放了火,导致网吧损失惨重，没几天后还被消防局勒令关了门。<br><br>不久后，3M金融互助崩盘，你的钱血本无归。这时候你才想起何子安当时的忠告。<br><br>没几天，何子安打电话来，他说他看了新闻，知道3M崩盘了，也知道你现在肯定不好过，他凑了2万元给你，让你拿着先顶顶，赶快找个工作吧。<br><br>你非常后悔，后悔当初为什么要干那样的傻事，何子安是真的为你好啊，你却为了一时之气烧了他的网吧坑害了他。<br><br>你本想告诉他事实，但又怕他怪你，更怕被安上一个“纵火罪”的名号，所以一直没有坦白此事。<br><br>至于那个女作者“雨慢漫”，你更是无脸告诉她你投资失败被骗的事情。但雨慢漫总是给你发私信，问你为什么不理她了。<br><br>你无奈之下只能杜撰了一个理由，说你交了新女友了，以后不想再联系雨慢漫了。<br><br>雨慢漫说她不信，还要看那个女孩照片，你无奈之下在自己的同学朋友圈翻找，恰好看到几张班花莫丽和一个英俊男子的自拍合影，于是你便挑了几张发给了雨慢漫。<br><br>雨慢漫过了好久，才回复了一句:“我知道了，祝你好运。”之后你和雨慢漫就再也没有了联系。<br><br>不久后，你为了生计，做了酒后代驾司机，虽然收入不高，但这行不会受人气，也比较自由，所以就这么一直干了下去。<br><br>你的薪水很低，一个月只有3000多，何子安平时经常会接济你，虽然不多，但也够你生活了，而且他从来没有要求过你还钱。你心里特别感动，心想这辈子有这么个兄弟真是三生有幸。<br><br>2016年，何子安说他朋友推荐了一个北冰洋保险公司的人身意外保险，100元就可以保500万，只要遭受意外，500万就是你的，他说他这辈子最好的兄弟就是你，他买了一份这个保险，受益人写的你的名字。还发了一份保险协议的照片给你看。<br><br>你感动的无以复加，也在北冰洋公司买了10份一样的意外保险，受益人也写下了何子安的名字。你给他说起这个事，他还怪你白花钱。<br><br>时间过的很快，2018年8月，何子安突然告诉你说他恋爱了，虽然你自己的对象还没着落，但仍为他感到高兴，便让他把对象照片发来，说你替他把把关。<br><br>何子安嘿嘿一笑，说是高中同学张萱萱。<br><br>张萱萱是一个很恬静善良的女生，他们很般配，你很高兴地恭喜了何子安。<br><br>2019年8月多,何子安突然告诉你他要和张萱萱订婚了,想请你和一些老同学来聚一聚。<br><br>你大概打听了一下，此次何子安和张萱萱邀请了马晓光，段大鹏，莫丽还有你的老同桌徐如雨。而这些同学一个个都混得人模人样。<br><br>何子安是XM大学化学系的讲师，张萱萱是明星私人化妆师，莫丽则是全国最大的酒吧ladyface 的主场驻唱歌手，徐如雨是著名的电视剧编剧，而刘克据说是国防部门的电脑安全工程师。连没有考上大学的段大鹏都成了一名给领导人搭配膳食的高级营养师。<br><br>再看看你自己，一个代驾司机而已……<br><br>不行，自尊心让你不允许以这样的身份出场这次同学会，尤其是徐如雨，她还以为你是家世显赫的富二代呢!<br><br>于是，你给自己编造了一个身份，摇身一变，变成了“继承了家族企业的富二代”，只要好兄弟何子安帮你瞒住大家就行了。<br><br>于是你给何子安打了电话，何子安沉默了一会儿，表示大家是兄弟，他一定帮你这个忙。<br><br>即使有何子安帮你圆谎，你自己也不能闲着，毕竟这次要见到你曾经追求的女人了，你不能像只单身狗一样孑然一身的去，得找个漂亮的女朋友镇住气场。<br><br>于是，你联系了一家名为“金不换”的男女友出租网站，想租一个美丽大方漂亮的女孩做“临时女友”。<br><br>但询问了介绍员价格之后你动摇了，要去何子安的城市，至少得3-4天，这里的女友出租，相貌稍微过得去的，一天就得2000元。你还得负担车费、住宿（酒店单独开单间)还有饮食的费用，下来怎么着也得1万元以上了<br><br>于是介绍员给你介绍了另外一种业务，就是“虚拟女友”业务。<br><br>所谓虚拟女友，就是找一个漂亮大方的女性和你进行多场景的合影，可以做一些男女朋友之间亲昵合影的动作（装样子)，只需要半天到一天时间就可以搞定。回头这些相片都可以存在你的手机里给家人或朋友来进行炫耀，就像你真的有了女友一样。<br><br>介绍员告诉你，有一个女孩各方面都很出色，前几天刚刚被2个人租用拍了“情侣照”，问你愿不愿意考虑她。<br><br>介绍员告诉你，这个女孩半天时间只需要1600元，也没有额外的费用。<br><br>你看了照片，很满意，所以你交了钱，让介绍的那个女孩做了你的半天“女友”，在手机里存了上百张合影。<br><br>这下再有何子安的配合，你总算有了去参加同学会的底气。<br><br>2019年9月1日，你如约出现在众同学的面前。<br><br>大家看起来都很健谈，和高中时期都有了一定变化。<br><br>最明显的是何子安，他的身高高了一大截，带着一副考究的金丝眼镜，配着一身笔挺西装，连高中时那颗河狸一样的大板牙也正常了，像一个文质彬彬的绅士。<br><br>张萱萱仍然是一头垂肩秀发，变得更加文静了。<br><br>徐如雨带上了圆框眼镜，看起来极有书卷气息，就是略显有些宅。<br><br>莫丽的妆容化的很漂亮，原先的单眼皮也变成了双眼皮，整体的穿戴看起来非常时尚。<br><br>刘克带着厚厚的眼镜，似乎综合了程序员和学者的气质。<br><br>段大鹏穿着一身休闲装，显得有些虎头虎脑，精神状态非常好。<br><br>十年未见，大家聊的很开心，包括何子安在内，没有任何一位同学炫耀自己的成功，大家只是在聊一些当年的情谊和往事。<br><br>晚上7点多，何子安提议去他们这里有名的【last night酒吧】喝一杯，顺便庆祝一下再次重相逢，大家都同意了。<br><br>8点，你们到了酒吧，一边喝酒一边聊天，不一会儿，一个带着墨镜，年轻漂亮的女孩子来到你们身边，她和何子安说了几句话，何子安介绍说这是他在大学里带的学生，名叫【安娜】，给大家认识一下。<br><br>接下来的事情你就记不得了……<br><br>你的任务:<br><br>1 隐瞒自己生活窘迫的事实（次要)<br><br>2寻找他人隐藏的秘密（次要)<br><br>3找到何子安死亡的真相，还