          script: [
            '你醒了过来，头有些晕沉沉的，你感到一丝莫名的尴尬，另外，你胸口居然有四个大字的纹身:葬爱无敌。<br><br>你放眼看去，自己现在正处在一处病房里，天微微亮，你周围有五个人，分别是2男3女。<br><br>这些人好像很眼熟的样子，对了，你能想起来，你们昨夜在一起喝酒来着，好像你们的关系还不错，但昨夜的记忆怎么一点都没有了呢?<br><br>不对，不光是昨夜,之前的记忆好像也乱七八糟的,你现在想起来你好像是一个重大国防项目的计算机安防员。除此之外，你能想起来的只有一些零零散散的片段。<br><br>这时候医生来了，他对你们说:“你们没什么大碍，签个字就可以出院了。”<br><br>你可以回忆起以下的记忆片段<br><br>【公交车】你在一辆公交车上，看到一个小偷正在把手伸向一个乘客的背包，那个壮硕男子突然站出来大喊了一声。<br><br>【奶奶】夜很黑，前边好像有个亮灯的诊所，那个壮硕男子感激的对一个老奶奶说:奶奶，真的太感谢你了，你以后就是我的奶奶。<br><br>【诊所】这里好像乱糟糟的，有个医生对着那个微胖男子（并没有鸡冠头）喝骂:你们再不走我就报警了。<br><br>【便利店】你对一个店员模样的人说:小票怎么不给我?<br><br>【计程车】你注意到那个计程车的车牌是:2B250。<br><br>【打架】一个气势汹汹的人指着微胖男子(并没有鸡冠头）骂:这家伙真他妈让人恶心。<br><br>【地铁站】你说:这个不尴尬，不用套袋子，我去买吧。<br><br>【地铁】你夺过那个杯子，一把将里边盛的液体泼在地上，郑重其事的对围观的人说:你们看，我说的没错吧!<br><br>【医院】一个医生严肃的说:谁让你们用这个的?不要命了啊?<br><br>【民宿】那个微胖男子(并没有鸡冠头)执拗的说:今天也让我兄弟骑我一次!我带 ta回来过夜怎么了，又不占床。<br><br>【电线杆】你看了看,感觉这个人似乎在哪见到过。<br><br>【餐馆】你有些尿急，去了洗手间，但在外边没找到开关，你一开洗手间门，里边突然闪出奇异的蓝色光芒，一股阴风飘了出来，你有些紧张，迅速解决了个人问题。<br><br>【餐馆门口】你给那个文质彬彬的男子说:“朝里走，左拐直走，有一扇门，不用开灯，一拉门灯就亮。不过那地方有股阴风，似乎不太干净，要不然一会儿找个公厕吧?”<br><br>【马路边】你自告奋勇的说道:晓光，这小子还挺横，我和你一起去。<br><br>【车神】一群留着杀马特造型的社会青年围着你们的车，歇斯底里的狂叫着:车神，计程车车神。<br><br>【赌注】你看着手里的透明盒子，惊讶的说:哇，这个家伙好威风啊，我好想带回去养。<br><br>【小费】微胖男子（有了鸡冠头）拍了拍司机，笑了笑说:得，这500算小费，就当借你车用了1个半小时。<br><br>【手表】你指着一间表行对那个漂亮女子(手腕上并没有画表)，说:莫丽，你不是说喜欢奢侈品吗，走，今天赚了点，给你买一块。<br><br>【忘了】你一拍脑门，叫道:倒霉，我把东西放在餐馆盆里忘带了。谁有联系方式?那玩意老值钱了!<br><br>【我的货呢】几个凶神恶煞的人围着你们，不停的问:“我的货呢?东西呢?交出来!”莫名其妙的你很害怕，躲在一边大气也不敢出。<br><br>【一起去HIGH】那个文质彬彬的男子说:“大鹏，晓光，刘克，莫丽，如雨你们几个都上来吧，安娜的车足够大，我来开车。”',
            '在催眠师的帮助下，你终于回忆起了你的过往……<br><br>你今年28岁，名字叫做【刘克】，生活窘迫而糟糕，因为你并不是什么【国防部门计算机网络安防员】，而是一个小小的网吧【网管】。<br><br>所谓的“计算机网络安防员”，是你编造出来的谎言，目的是为了应付这次的高中同学会，因为同学们一个个都混的有模有样，只有你是最不如意的一个。<br><br>想当年，你们都是华美高中的学生。<br><br>你学习不怎么样，但特别喜欢钻研电脑，懂得很多电脑里软件的用法，还很喜欢玩网络游戏。不过你家庭条件很一般，你并没有太多的闲钱去上网，一周最多只能玩几个小时。<br><br>高中时期，你们班有个男生叫做【何子安】，长的矮墩墩的，戴一副黑框眼镜，两个门牙特别突出，性格十分胆小内向。<br><br>何子安和班里的同学【马晓光】关系比较好。<br><br>马晓光是一个不苟言笑的学生，听说父母是做生意的，他的身材和性格倒是和何子安有几分相似。<br><br>因为马晓光家庭条件不错，所以你尝试接触马晓光，想带着他一起玩游戏，这样你就有了赊账的靠山，但马晓光对游戏的热情度不高，很快就失去了兴趣。<br><br>通过马晓光，你和何子安也熟悉了起来，你教何子安打网络游戏，带他熟悉游戏里的人物，带他一起和别人 PK，打竞技。<br><br>不过何子安技术很差，你总是说他“笨手笨脚”“傻子”“猪队友”，他似乎也不以为意。<br><br>因为你经济条件不好，所以经常会向何子安借钱上网打游戏，何子安也很痛快大方，从来都不让你还钱，为此你对他很是感激，心想等你以后工作赚钱了一定加倍还给他。<br><br>你在网吧里算年龄比较小的，有时候一些社会青年会问你借钱，你自己花都不够，哪里有钱给其他人。但那些社会青年依然不依不饶的，甚至还把你拉出网吧打了一顿。<br><br>无奈之下，你只能答应他们，说你下次带着同学来，你的同学有钱，让这些社会青年可<br><br>以从你的同学那里要。<br><br>当然，你说的“同学”就是何子安。<br><br>第二天，你拉着何子安去网吧，那些社会青年早就等在那里了。他们把何子安带出去，一直都没有回来。<br><br>第二天，你上学的时候见到了鼻青脸肿的何子安。他那天都没怎么说话。<br><br>从那天起，他就不怎么和你玩了。你从此也断了一大部分经济来源。<br><br>何子安的体育成绩非常差，几乎没有什么运动能力，文科成绩中上，但数理化成绩极其拔尖，次次考试都几乎可以达到满分，连老师都说何子安是未来的科学家。<br><br>你们班里有个叫【段大鹏】的，生的人高马大，他是学校里的霸王，经常欺负班里的同学，尤其喜欢欺负何子安，还给何子安取了一个外号叫“河狸”。很快这个外号就在班里叫了起来。<br><br>何子安偷偷的告诉你，说这个段大鹏经常会敲诈他零花钱，但你也有些怕他，所以只能安慰何子安最好少招惹他，惹不起咱们就躲。<br><br>班里的学习委员叫【徐如雨】，她是一个要强的女生，文科成绩相当不错，但她的数理化成绩差何子安很多，导致每次考试总分都在何子安之下，所以能感觉出来她对何子安有一股敌意。<br><br>班花叫【莫丽】，几乎全班的男同学都对她表现出明显的好感，你也对她很有好感，但你颇有自知之明，像你这样长得不帅，学习差，家没钱，还很贪玩的男生实在没什么可取的地方，她不可能喜欢你。<br><br>班里还有一个叫【张萱萱】的女生，她是文体委员，也是一个乐于助人的善良女生。她有一头乌黑发亮的长发静静垂在肩头，显得非常文静。段大鹏好像很喜欢张萱萱，经常都会出神的盯着张萱萱看。<br><br>何子安最怕的事情就是上体育课，所以每当有户外活动的时候何子安都会尽量避免，为此，作为文体委员的张萱萱还专门批评过何子安。<br><br>这个何子安还真是不省心，不久后又听班里的同学说他给班花莫丽写了封信，莫丽当场就把那封信撕了。有好事的同学从垃圾堆里拼凑出了何子安的信,发现居然是一封情书，<br><br>这下子何子安成了全班人眼中的笑料，有人还说他“癞河狸想吃天鹅肉”。<br><br>别看这何子安性格内向闷骚，但真还有点小聪明，有次老师在全班通报批评，说何子安谎称发烧来逃避上体育课，其实他是用袋子里的生石灰加水提高温度，然后把额头烤的烫烫的再让老师感受他的“发烧”。老师说这种小聪明不可取，当场就罚何子安在班里做了30个俯卧撑。<br><br>以何子安的身板，别说30个，就是3个都做不下来，所以几乎全班同学那天都看了他的笑话。你记得笑的最得意的就是学习委员徐如雨了。<br><br>第二天，徐如雨突然在班里大哭起来，还说要是谁损坏了爸爸的照片，她就和谁拼命。<br><br>过了2天，何子安突然找你，要你教他用photoshop (PS做图)，这是你和何子安搞好关系的机会，你答应了他，费了2天功夫教会了他，虽然你不理解何子安为什么突然要学这个，但你想也许是和徐如雨丢失的照片有关吧。<br><br>又过了2天，你看到何子安给徐如雨塞了什么东西，然后徐如雨扇了何子安一个耳光，怒声说有什么事你冲我来，别动我爸爸留给我的东西。<br><br>日子一天天飞速的过着，你和何子安又渐渐好了起来，高三的时候，学校加入了微机课。<br><br>不过学校的电脑太烂，根本跑不起来你玩的游戏，最多就是聊聊QQ，上.上网。<br><br>有次你在微机课时看到段大鹏和张萱萱在聊天，好奇的你想知道他们之间的关系，便找机会在段大鹏的电脑里装了木马程序，盗取了段大鹏的QQ。<br><br>你登录上段大鹏的QQ后，发现段大鹏居然在和张萱萱在交往。你模仿段大鹏的口气从张萱萱那里探听出二人虽然没有男女之间实质性的关系，但也已经是比较亲密的男女朋友了。<br><br>你想到段大鹏平时这么飞扬跋扈，不可一世，今天刚好可以用下“离间计”让他也吃点苦头。<br><br>当下你就模仿段大鹏的口气告诉张萱萱说段大鹏其实喜欢的是班花莫丽，只不过莫丽太优秀，和张萱萱交往只是下下之选。<br><br>听了“段大鹏”的自白，张萱萱愣了，好久都没有回复，你怕张萱萱打电话向段大鹏求证，心也虚了，连忙退出了段大鹏的QQ。<br><br>但没想到你的离间计居然起效了，第二天就看到张萱萱坐在自己座位上发呆，而段大鹏也是面带愧色，从那之后你再也没有见他们说过话。<br><br>没两天，你注意到班花莫丽在课间时期红着脸偷偷的在段大鹏书包里塞了一张纸，你很好奇，趁着教室没人，连忙偷偷拿出来了。<br><br>打开信纸，上边写的是:大鹏，其实我早就喜欢你了，咱们班的那些男生只会低声下气的讨好我，只有你很特殊，你打篮球时的英姿一直在我脑海里晃动。马上就要高考了，很快同学们就会分道扬镳，我赶在高考前想对你说一声:我喜欢你，愿意做你的女朋友,不管你喜不喜欢我，不管你是不是对我不屑一顾，我都不会后悔今天向你做出的表白。<br><br>信纸后边的签名是莫丽。<br><br>看了这封简短的信后，你妒火中烧，你促成了张萱萱和段大鹏的分手，但却没想到你的女神莫丽居然喜欢段大鹏，这个段大鹏欺负同学，学习还差，就是会打打篮球，有什么好?<br><br>你绝不能让段大鹏看到这封信，所以你偷偷的藏起了这封信。但你怕莫丽再用其他方式去表白，所以你必须要让第三方的力量介入。<br><br>于是你把段大鹏和张萱萱的聊天记录导了出来，截图以匿名的方式寄到了班主任电子邮箱。<br><br>很快，段大鹏就被班主任叫了过去，你躲在门口隐约听到老师在怒吼:“……段大鹏，张萱萱是很有希望考上名牌大学的，你这个差生不要影响张萱萱的学习，而且学校已经通知了你的家长……”<br><br>这样段大鹏早恋的事情就会曝光，莫丽也许就会望而却步了。<br><br>这件事情过去后，莫丽并没有和段大鹏有什么发展。你看事情差不多平息了，还拿出莫丽写给段大鹏的那封告白书给马晓光和何子安炫耀，当然，你提前将莫丽写的“大鹏”两个字撕掉，把中间那句“你打篮球时的英姿一直在我脑海里晃动”涂掉了“打篮球时”四个字，变成了“你****的英姿一直在我脑海里晃动”。<br><br>何子安倒没什么反应，但看到马晓光表现出“你小子行啊，班花都和你表白了!”的表情，你心里别提有多得意了。<br><br>2008年，毕业了，大家进行了高考。<br><br>何子安考的很好，被保送上了国内有名的XM大学。<br><br>马晓光考的一般，上了一个普通的二本。<br><br>你这次超常发挥，居然超了分数线好几十分，成功的被GZ财经大学录取，老爸老妈着实夸了夸你，还奖励了你一个IPhone手机。你没有舍得立刻使用，而是包起来想等自己手机坏了再使用。<br><br>至于那个段大鹏，据说连分数线都没考到，回去乖乖种田吧!<br><br>上了大学之后，你和何子安经常还有联系，这时候何子安已经不怎么玩游戏了，他偶尔会给你发一些他的自拍，如今的他越长越帅，个头也窜了很多，一下成了一个帅小伙。<br><br>你依然喜欢玩游戏，而且还有了几个志同道合的舍友，因为大学纪律管的松，所以你们更是变本加厉日以继夜的玩网游，逃课是家常便饭，通宵是习以为常。大学的专业课也学的一塌糊涂。<br><br>为了玩游戏，你那点生活费很快就见了底儿，于是你选择了在网吧做兼职，赚点钱的同时还能免费玩，你去了你们学校外边不远的“梦飞翔网吧”做了网管，你的编号还很帅气，刚好排到了“007”，你的舍友和网吧员工们都开玩笑叫你“邦德”。<br><br>有次你上班时，突然看到了一个熟悉的身影。<br><br>那是莫丽，她居然也在这个城市……<br><br>她还是一如既往的美丽诱人