            '你醒了过来，头有些晕沉沉的，胃里也有些不太舒服。你放眼看去，自己现在正处在一处病房里，天微微亮，你周围有五个人，分别是3男2女。<br><br>这些人看起来好像很眼熟的样子，对了，你能想起来，你们昨夜在一起喝酒来着，好像你们的关系还不错，但昨夜的记忆怎么一点都没有了呢?<br><br>不对，不光是昨夜﹐之前的记忆好像也乱七八糟的，你现在想起来你是一个非常厉害的电视编剧。除此之外，你能想起来的只有一些零零散散的片段。<br><br>这时候医生来了，他对你们说:“你们没什么大碍，签个字就可以出院了。<br><br>你可以回忆起以下的记忆片段<br><br>【公交车】你在一辆公交车上，看到一个小偷正在把手伸向一个乘客的背包，那个壮硕男子突然站出来大喊了一声。<br><br>【奶奶】夜很黑，前边好像有个亮灯的诊所，那个壮硕男子感激的对一个老奶奶说:奶奶，真的太感谢你了，你以后就是我的奶奶。<br><br>【诊所】这里好像乱糟糟的，你对那个文静女子低声说:晓光今天好英勇啊。<br><br>【便利店】你对那个消瘦眼镜男说:你傻啊，套个黑塑料袋啊!<br><br>【计程车】计程车司机像看傻子一样看了看那个壮硕男子。<br><br>【打架】一个文质彬彬的男子劝架说:大家都是成年人了，有事讲理，别打架!<br><br>【地铁站】你看着手上的一堆一元钢铺，陷入了沉思。<br><br>【地铁】你对那微胖男子(并没有鸡冠头)纠正道:我看像是追剧追的多导致的。<br><br>【医院】一个医生说道:你们这些年轻人，一点医疗常识也没有。幸亏来得早，没什么大碍。<br><br>【民宿】你对那微胖男子（并没有鸡冠头）说:去去去，我这作诗呢，那边有个镜子，你自己弄!<br><br>【电线杆】你捡起那个文质彬彬男子扔在地上的纸团，放在了包里。<br><br>【餐馆】你从包里取出纸团打开看了看，点了点头，说:绝对是他，痣都长一个位置。萱萱，你打电话通知他家人。<br><br>【餐馆门口】你自言自语的说:拜关公有什么好?又不能要什么来什么。<br><br>【马路边】你拿出那张寻人启事，说:没错，这症状很对路。<br><br>【小费】微胖男子(有了鸡冠头）拍了拍司机，笑了笑说:得，这500算小费，就当借你车用了1个半小时。<br><br>注意:这时候你清醒了，以下的事情你能想起来，但你不能让其他人知道你是第一个清醒的，否则大家很容易发现你杀了人!所以下边几条信息你还得继续装作醉酒的样子<br> <br>【真实有关“美容院”的回忆、不可以告诉大家】你对那漂亮女子说:“买什么炸弹?你疯了啊?”那漂亮女子并不听你的话，而是一个劲的要炸美容院。你看到一个“大发小卖部”，灵机一动，对她说:“你呆着别动,我给你买炸弹去！”你买了一副扑克，把牌盒里的四个花色的A——K全部4个4个都凑在一起交给了她，说:这13个炸弹够炸美容院了，你去放炸弹吧。<br><br>（注意:你可以编造有关【美容院】回忆来骗过大家，让大家认为你当时也属于片段性记忆状态。)<br><br>【真实有关“我的货呢”的回忆不可以告诉大家】几个凶神恶煞的人围着你们，不停的问:“我的货呢?东西呢?交出来!”大家似乎都有些紧张，但那个文质彬彬的男子把那几个人拉到一旁低声说了些什么，你听到他说“虽然鬼哥被条子抓了，但货我不会少了你们的。”然后文质彬彬男子就从包里掏出了一包东西给了那几个凶神恶煞的人。那几个人满意的离开了。<br><br>(注意:1、你对文质彬彬男子很有熟悉感。仔细一想，你想起来这次参加聚会就是因为你染上了毒瘾，这个男子就是你的同学何子安，他说你只要参加这次聚会，他就会给你够吸十年的冰毒<br><br>2、你可以编造有关【我的货呢】回忆让大家认为你当时也在片段性记忆状态。)<br><br>【真实有关“一起去HIGH”的回忆、不可以告诉大家】文质彬彬男子搂着一个昏昏欲睡的年轻姑娘，说大家难得见一次面，要玩个痛快，要一起去他们这里的酒吧玩通宵。大家都同意。但文质彬彬男子说车坐不下七个人，要文静女子自己打车回家。但文静女子不愿意，说要和你们在一起。文质彬彬男子劝了几次，文静女子就是不同意。于是文质彬彬男子让她和你们坐在后车，你说:“何子安，你什么时候把你答应给我的东西给我?”他说:你别急，到了地方就给你。你又问:“何子安，你怎么不和张萱萱坐一辆车啊?”文质彬彬男子说:这个城市萱萱比你们熟悉,我和她一辆车怕你们跟丢。然后，文质彬彬男子抱着那个似乎已经睡着的年轻姑娘上了前边的车，让你们的车跟着他。<br><br>(注意:你可以编造有关【一起去HIGH】回忆让大家认为你当时也属于片段性记忆状态。千万不要自爆吸毒。)<br><br>【以下是你的独有回忆千万不要告诉任何人】你们的车一直跟着文质彬彬男子的车，但是在路上的时候，前车突然停在了路中间，就在这时，一块石头从山上滚落下来砸穿了前车的挡风玻璃。你们的车踩了刹车，但还是追尾撞上了前车。虽然撞击并不严重，但除了你之外，你们车上的5个人似乎都失去了意识。你跳下车跑到前车跟前，发现文质彬彬男子和那个陌生姑娘似乎都昏迷了，而他的包里露出了一大袋白色的粉末，你立刻认出来，这是你赖以生存的“冰毒”。这一大包够你吸十年了，你伸手把他的包抢过来。但就在这时，文质彬彬男子醒过来了，原来他没死，他瞪着你，说:“徐如雨，你想抢我东西?你想逃出我的手掌心?做梦!除非我死了!”你猛的想起来，是这个家伙害你惹上了毒瘾，他还一直拿毒品来控制你，既然他不放过你，那么……你捡起从山上滚落下来的一块尖石，狠狠地砸在文质彬彬男子的头上，他惨叫一声，顿时脑浆迸裂而亡。就在这时，那个陌生姑娘醒来了，她看到你杀人，尖叫起来，你一不做二不休，也想去杀掉她。她吓得从车窗爬出来就跑，但她受了伤跑不快，你追上去，也拿石头砸死了她，并且把她的尸体推到了公路旁的悬崖下。然后你将得到的冰毒藏在了路边的草丛中，回到了刚才的后车上装作晕了过去。直到有车路过，一个好心人打了120把你们送到了医院……你昨晚喝了太多酒，所以也昏昏沉沉的睡着了……(接开始)'
            ,
            '在催眠师的帮助下，你终于回忆起了你的过往……<br><br>你今年28岁，名字叫做【徐如雨】，生活过得很平凡，因为你并不是什么【著名编剧】，而是一个【染上毒瘾名不见经传的网络文学写手】。<br><br>所谓的“著名编剧”，是你编造出来的谎言，目的是为了应付这次的高中同学会，因为同学们一个个都混的有模有样，只有你是最不如意的一个。<br><br>想当年，你们都是华美高中的学生。<br><br>你父母在你8岁时就离了婚，你还记得爸爸对你极其疼爱，但妈妈是一个特别风流的女人，身边总有很多男人围绕。爸爸一怒之下就和妈妈离了婚。<br><br>离婚后，本来你应该判给爸爸，但厄运突然降临，爸爸突发疾病去世了。你的抚养权又回到了妈妈这里。`<br><br>妈妈几乎把所有关于爸爸的遗物都销毁了，只有一张照片被你偷偷的保留下来。<br><br>这张照片是爸爸差不多10年前照的，那时候的他穿着帅气的大衣，整个人看起来非常阳光。<br><br>你把这张照片藏在了文具盒里，每天都会看很多遍。<br><br>你的学习很用功，各科成绩都还不错，尤其是文科更是名列前茅。<br><br>班主任挺喜欢你，便让你做了全班的学习委员，这也是让你很骄傲的成就。<br><br>你们班有个男生叫做【何子安】，长的矮墩墩的，戴一副黑框眼镜，两个门牙特别突出，性格十分胆小内向。<br><br>何子安的文科成绩一般，但理科成绩非常优秀，次次考试都几乎可以达到满分，连老师都夸何子安是未来的科学家。<br><br>你的文科成绩虽然不错，但也比何子安强不了太多，但何子安的理科要比你厉害的多，导致你每次考试总分都在何子安之下，所以你十分妒忌何子安抢了你这个学霸的风头。你们班里有个叫【段大鹏】的，生的人高马大，他是学校里的霸王，经常欺负班里的同学，尤其喜欢欺负何子安。<br><br>因为何子安看着憨憨的，两个门牙特别突出，胆子还小，非常像动物世界里的一种动物，你和班里的同学聊天的时候有意无意的就会说何子安好像某动物时，恰好被段大鹏听到，他一拍脑门，说何子安像河狸呀!<br><br>大家也都觉得这个形象很像何子安。段大鹏给何子安取的外号“河狸”很快就在班里叫了起来。<br><br>你看到何子安被欺负心里就莫名的舒服，谁让他学习那么好害自己被压一头?<br><br>何子安和班里的同学【马晓光】和【刘克】关系比较好。<br><br>马晓光是你的同桌，他有些不苟言笑，听说父母是做生意的，他的身材和性格倒是和何子安有几分相似，但学习成绩可就差远了。你有些看不起他。<br><br>而刘克是一个差生，他非常喜欢打游戏，为此老师都找过他家长好几次了，不过听说刘克是一个电脑方面的高手。<br><br>你有一个好朋友叫【张萱萱】，她是班里的文体委员，也是一个乐于助人的善良女生。她有一头乌黑发亮的长发静静垂在肩头，显得非常文静。虽然张萱萱并不是你们班的班花，但是那个“小霸王”段大鹏好像喜欢她，经常都会出神的盯着张萱萱看。<br><br>何子安五短身材，运动细胞几乎没有，特别讨厌上体育课。一到体育课就会以各种借口逃避运动。为此，身为文体委员的张萱萱批评过好多次何子安，可何子安还是我行我素。<br><br>另外就是你们班的班花【莫丽】，莫丽的学习中等，但长相非常漂亮，她唯一美中不足的是没有双眼皮，不过身材，五官，个头等其他方面都非常优秀。你和莫丽的关系也不错。<br><br>同桌马晓光对你不错，经常给你带好吃的零食，你开始不想受他的小恩小惠，但偶尔尝了一下，发现他带的零食还挺好吃的。<br><br>而且如果老师发现了你们吃零食的事情，他也总是将错误揽在他自己身上。这倒让你对马晓光有了些好感。<br><br>不久后的一天，马晓光突然告诉你，说他有些喜欢你，你白了他一眼，说:“学生以学业为重，你别胡思乱想，否则我就告诉老师去!”<br><br>那几天马晓光心情明显不好，你也没有安慰他。因为他学习很一般，并不是你喜欢的那种才子型的学生。<br><br>过了段日子，出了一个班级爆炸消息，听班里的同学说何子安给班花莫丽写了封信，莫丽当场就把那封信撕了。有好事的同学从垃圾堆里拼凑出了何子安的信，发现居然是一封情书，这下子何子安成了全班人眼中的笑料，有人还说他“癞河狸想吃天鹅肉”。<br><br>何子安出了丑，你心里别提有多痛快了，小样!谁让你学那么好!<br><br>有次你发现何子安偷偷在袋子里藏了生石灰，然后加水后生石灰会发热，他用这个方法捂在额头上，在体育课开课时把袋子藏起来，给老师说他“发烧”了。老师一摸他的额头，信以为真，便就让他留在教室里休息。<br><br>你偷偷把他的伎俩告诉了班主任老师，老师当场就罚何子安在班里做了30个俯卧撑。<br><br>以何子安的身板，别说30个，就是3个都做不下来，所以几乎全班同学那天都看了他的笑话。<br><br>第二天，你突然发现文具盒里的父亲照片不见了，你翻遍了书包和桌洞，都没有找到。<br><br>你急的大哭起来，这一定是有人恶作剧拿走了你爸爸的照片，你放出狠话说要是谁损坏了爸爸的照片，你就和谁拼命。<br><br>过了几天，何子安突然把你叫到一旁，塞给你一样东西，说了声:“徐如雨，我找到了一张照片，你看看是不是你爸爸的照片。”<br><br>你看了一下，果然是父亲的照片，一定是这个何子安得知之前的“发烧事件”是你给老师说的，所以才要报复你故意拿走你父亲的照片让你着急。<br><br>你看了下照片，还好没有什么磨损，但你还是气不打一处来，直接给了何子安一个耳光,怒声说有什么事你冲我来，别动我爸爸留给我的东西。<br><br>班里很多同学第一次见你打人，都惊呆了。张萱萱问清楚情况后也劝慰了你，你才渐渐消了气。<br><br>没过多久后，张萱萱红着脸告诉你她喜欢上段大鹏了，而且已经开始和段大鹏偷偷交往了。你非常八卦的问东问西，还笑张萱萱是早熟，还说你们班的男生都太俗，没有能入你法眼的。<br><br>你的二叔徐晓明在市里的乐器城开了一间乐器店，店名叫“徐徐好民谣”，因为二叔和你的父亲长得非常像，虽然妈妈反对，但你仍经常会偷偷的到二叔那里玩。<br><br>有次你去二叔那里，看到了段大鹏也在和你二叔说话，段大鹏看到你后显现出极为不自然的表情，打了个招呼就告辞了。你二叔告诉你说段大鹏闲暇时间会来他店里帮工。<br><br>你心想从穿着基本能看出来这个段大鹏家里条件一般，他打工赚点钱花也没什么好隐瞒的，看来他自尊心还挺强，哈哈。<br><br>没多久后，你注意到张萱萱无精打采的，经过你的不懈询问，张萱萱哭着说段大鹏移情别恋了，他要去找班花莫丽告白。<br><br>你吃了一惊，不过还是表示理解，安慰张萱萱说男人都是颜值控，让她别伤心了，段大鹏学习那么差，以后不会有什么前途的。<br><br>你心想这个段大鹏心还挺沉，得到了张萱萱居然还不满足，还想要莫丽?不过你也有些好奇，便去找了莫丽，将段大鹏喜欢莫丽的事情告诉了她。<br><br>听了你的话，莫丽瞪大了眼睛，似乎是非常吃惊，但惊讶中似乎还带着窃喜。<br><br>下午的时候，你作为学习委员去老师那里拿作业，刚好老师在打电话，你隐约听到:“段大鹏爸爸，你儿子早恋是很严重的，影响了自己不说，还要影响其他同学，这马上就要高考了，我作为班主任不希望发生影响学生心情的事情，希望你能来一趟学校，我就你儿子的情况说一说。”<br><br>你匆匆拿了作业就出去了，心想老师这消息也太灵通了，难道是张萱萱气不过莫丽夺走她男朋友，愤而去老师那告状了?<br><br>你把听到老师打电话说的话一五一十的告诉了莫丽，莫丽吓的直吐舌头。<br><br>很快就迎来了高考，你发挥的还不错，上了你心仪的大学，不过你的心志很高，也没有表现出多得意。何况何子安比你考的好的多，被保送上了国内有名的XM大学。可能是因为失恋的打击，张萱萱发挥有些失常，最后仅仅只过了分数线几十分，上了一所普通大学。<br><br>莫丽考的很普通，上了Gz财经大学。<br><br>而那个段大鹏连分数线都没过，最后好像去了什么烹饪学校。<br><br>大学的生活很枯燥，酷爱文学的你进了好几个学校的文学社团，认识不少靠笔杆子赚money的年轻作家。<br><br>不听不知道，一听吓一跳，这些年纪和你相仿的作家在×江文学网连载小说，月入高的有五六来万，低的也有一两万。<br><br>你心想凭自己的文学造诣一定可以混出个所以然。所以在大二时期就开始以“雨慢漫”的笔名连载历史架空小说《残照又当楼》。<br><br>但你的书却没什么人气，你叹息大家不识货，只会看那些垃圾文学，也暂时停止了写小说。<br><br>很快，你大学毕业了，刚刚步入社会的你没有什么工作经验，尝试性的找了一家化妆品公司（盛颜公司）做销售。<br><br>令你意外的是，你的高中同桌马晓光居然也在这家公司做销售。<br><br>马晓光的身材没变，还是矮墩墩的，胖胖的脸上挂满和气的笑容，你们都在感叹世界真小。<br><br>成了同事后，马晓光经常会约你一起吃饭，因为只是普通的工作餐，外加马晓光在公司里业绩不错，你想向他学习一些销售经验，所以一般不会拒绝。<br><br>但你对销售这行实在没有天赋，导致业绩一直很差，公司月会时经理狠狠的批评了你，还放出狠话，让你下个月不提升业绩就不要来了。<br><br>就在这时，马晓光突然站起来掀了桌子。<br><br>他走到经理面前，大声批评着领导，说这个盛颜公司都是他们家投资的产业，他来这里只是为了锻炼，还让经理快点给你道歉，否则他声称会动用关系开除经理。<br><br>经理的脸上青红不安，他知道惹不起这样的富二代，当即就给你道了歉。<br><br>当天下班，马晓光邀请你一起去看电影。<br><br>看在他今天这么仗义的份上，你同意了，但你也知道他在想什么。<br><br>在电影结束的时候，马晓光果然向你表白了，但马晓光真的不是你喜欢的类型，所以你最终给他发了好人卡，拒绝了他。<br><br>为了不让双方难堪，你第二天就递交了辞职报告，说自己不是干销售的料，让马晓光不用为你的事费心。<br><br>马晓光什么也没说，看得出来他想挽留，但却不知道挽留的意义，最终他只是苦涩的笑笑，任你而去。<br><br>离职以后，没有一技之长的你又拾起了你的小说，你日以继夜的写，把《残照又当楼》每日都保证更新1万字左右。<br><br>但人的灵感是有限的，有时候你搜肠刮肚就是写不出来几句话，于是你开始服用苯丙胺(兴奋剂）来刺激自己创作，一时间很有效果。<br><br>你把《残照又当楼》一口气写了200多万字，阅读量也有一些增长，但总体还是平平，依靠写小说每个月也就只有2000多元的收入。<br><br>但你不信这个邪，你知道大神都是磨炼出来的，所以一直坚持每天更新1万字。<br><br>母亲很看不惯你，一直督促你找工作，但因为爸爸的事情，你从小就很叛逆，母亲越这么说，你就越要写出个名堂给她看看。<br><br>你的好友张萱萱大学毕业后回来了，她如今在一所美容院工作，有时候会来你家玩，还会夸你妈妈漂亮。<br><br>一来二去,张萱萱不知怎么的就说动了你妈妈去做美容。你妈妈一向很在意自己的容颜，几乎每周都要去美容院花好几百块保养。<br><br>虽然妈妈花的都是她自己的钱，但你也不太高兴，心想美容院都是大忽悠，张萱萱也真是的，拉客户怎么能拉好朋友的妈妈呢?<br><br>就在这个时间，你的小说读者中有两个新进粉丝，一个叫“创世一哥”，一个叫“宅男好可乐”，他们2个通过网站的打赏系统给你打赏了好几次价值几千元的大礼包，创世一哥甚至还在网站私信你，说他是个金融投资家，月入十万，特别喜欢你的小说，觉得你的作品可以傲视如今的阅读网站。他还说想追求你。<br><br>你从来没有遇到过这么欣赏你才华的人，所以一下就被“创世一哥”的诚挚打动，而且网恋也不是很尴尬，便答应了他。<br><br>你告诉“创世一哥”，说你大学毕业后放弃了薪酬不菲的工作，而是选择追求自己的梦想——写小说。他更加欣赏你了。<br><br>你的小说收到了很多次“创世一哥”的打赏，足足有好几万，你幻想着你们有朝一日能够见面，然后共坠爱河。<br><br>没过多久，这个“创世一哥”突然不再联系你了，你慌了神，连连发私信询问他在做什么。<br><br>经过你多日的询问，他终于回复了，他说他找了女友，以后不会再和你来往了。<br><br>你不敢相信这个事实，你问他要那个女友的照片，让他证明。<br><br>结果他真的发过来几张照片，你一看照片就呆住了。照片上的男子是一个高大帅气的陌生阳光男生，而女子不是别人，正是你的高中同学，班花莫丽。他们在照片上做出很亲昵的动作，一看就是情侣。<br><br>你沉默了好久，才回复了一句:“我知道了，祝你好运。”之后你和“创世一哥”就再也没有了联系。<br><br>那段时间没有了“创世一哥”的支持，你非常苦闷，而且还得保持小说更新的字数，外加你长期服用兴奋剂，已经有了一定的抗药性，现在就算吃兴奋剂你的创作灵感也不能思如泉涌了。<br><br><br><br>不过你的读者里还有“宅男好可乐”一直在支持着你的小说。他经常和你私聊，你把你的苦恼都告诉了他。<br><br>宅男好可乐说他有办法，很快他托人给你带来了一小包胶囊让你服用，这胶囊里装了一点白色粉末，宅男好可乐说这个比你服用的兴奋剂药效好，而且还没有副作用。<br><br>你这时候已经很信任他了，所以就开始尝试他推荐的药。<br><br>果然，服用了他的药后，你顿时感觉升入了天堂，各种文思泉涌，每天恨不得一口气更新写2万字。你的小说读者一下子多了不少。<br><br>你恨莫丽不声不响的就“抢走”了你的男朋友，但你也无处撒气，因为你和“创世一哥”一直都是用笔名在网上交往的。莫丽也不知道你在网上的笔名。<br><br>2015年,莫丽联系你说她快要结婚了,还给你发过她男友的照片，正是那个“创世一哥”，还让你不要忘记参加她的婚礼。<br><br>你心里妒火燃烧，但也没有办法。<br><br>几天后，莫丽突然问你知道哪家美容院割双眼皮效果好吗?她说她是单眼皮，眼睛不显大，想割双眼皮。<br><br>你心里突然冒出一个恶毒的想法。<br><br>于是，你把莫丽的联系方式给了张萱萱。<br><br>不久后，莫丽定好了做手术的日子，张萱萱给她专门找了美容院的主刀医生。<br><br>你拿出莫丽的照片，指着告诉妈妈，说就是她抢了你的男友，让妈妈去美容院做美容的时候提前把你之前没有吃完的兴奋剂偷偷倒在那个主刀医生喝的水里。<br><br>妈妈答应了你，第二天，就听说手术做砸了，莫丽的眼皮被割歪了。<br><br>然后主刀医生被开除，张萱萱好像也受了牵连离职了。<br><br>至于莫丽，她虽然不算毁容，但眼睛现在看起来无比的别扭，原本至少90分的颜值，现在顶多只有70分了。<br><br><br><br>莫丽那段时间都是以泪洗面,她交的男朋友都准备结婚了,就因为这个事情和她分手了。为了此事，你也有些自责，但既然已经这样了，也没有什么补救的余地了，你也不可能赔偿她什么。这就是命吧!张萱萱离职后又找了几次工作，但都没干多久，后来她去了外地，你们就没怎么联系了。<br><br>你依然写着你的小说，每天都会依靠宅男好可乐给你的药物来维持你的灵感。虽然你的粉丝群体增加了不少，但宅男好可乐声称这种药物不好搞，每一粒胶囊就得收你300元钱。<br><br>所以你从小说上赚取的稿费基本都买了宅男好可乐的药。<br><br>时间长了，你醒悟过来，他给你服用的一直是毒品啊!<br><br>但这时候你已经有很大的毒瘾了，根本无法离开毒品。所以你只能依靠宅男好可乐给你的药物来过着这样行尸走肉般的生活。<br><br>你恨自己为什么一不小心就染上了毒瘾，你也尝试过戒毒，但总是无法戒掉。<br><br>你恨自己，你在自己每次毒瘾发作的时候都会拍下自己满脸鼻涕眼泪的可憎照片，以此为警钟提醒自己，但毒品真的是魔鬼，一旦你离开它，你就感觉坠入了万劫不复之地。只有继续服用才能给你带来安宁。<br><br>你把这些照片存在了自己手机隐藏相册里。<br><br>2019年，张萱萱突然打来电话，说她快要订婚了，她邀请了一些同学，让你也来她的城市聚一聚。<br><br>你问她新郎是谁，张萱萱怪不好意思的说是高中同学何子安。<br><br>哇靠，他们怎么搞在一起的，想当年那个矮胖子何子安还给莫丽写过情书呢!<br><br>张萱萱请你去参加聚会，你大概打听了一下同学们的情况，听她说大家如今都混的不错。<br><br>只有你混得最惨，不光收入很低，还染上了毒瘾。你不想参加这样的聚会。于是你拒绝了张萱萱，推说自己没有空去不了。<br><br>但何子安很快就给你打了电话，他神神秘秘的说他知道你不去的原因，并且说你只要参加这次聚会，他就会给你一大包你赖以生存的冰毒，足够你吸食10年。<br><br>你不知道何子安为什么会知道你染上毒瘾，但他给你的许诺让你动了心。所以你在何子安的利诱下又给张萱萱打了电话，说明你改变主意了，你愿意去参加聚会。张萱萱很高兴。<br><br>通过打听，你知道张萱萱是明星的私人化妆师，何子安是XM大学的讲师，莫丽成了全国最大连锁酒吧ladyface的主场主力歌手。刘克现在是国防部门的电脑安全工程师。段大鹏成了一名给领导人搭配膳食的高级营养师。至于马晓光，他已经继承了家族产业，成了一名实力富二代。<br><br>你不能这样丢份儿，于是你给自己杜撰了一个身份“著名电视剧编剧”，反正他们也不接触这行，骗人骗鬼都一样。<br><br>2019年9月1日，你带上了圆框眼镜，让自己看起来有些书卷气息，然后坐上了去张萱萱城市的飞机。<br><br>下午，你如约出现在众同学的面前。<br><br>大家看起来都很健谈，和高中时期都有了一定变化。<br><br>最明显的是何子安，他的身高高了一大截，带着一副考究的金丝眼镜，配着一身笔挺西装，连高中时那颗河狸一样的大板牙也正常了，像一个文质彬彬的绅士。<br><br>张萱萱仍然是一头垂肩秀发，比高中时期甚至变得更加文静了。<br><br>莫丽的妆化的很漂亮，她割坏的双眼皮也修正了不少，整体的穿戴看起来非常时尚。<br><br>段大鹏穿着一身休闲装，显得有些虎头虎脑，精神状态非常好。<br><br>刘克带着厚厚的眼镜，似乎综合了程序员和学者的气质。<br><br>马晓光身材还是老样子，略微矮胖，但整个人萦绕着一股自信的气场。<br><br>十年未见，大家聊的很开心，没有任何一位同学炫耀自己的成功，大家只是在聊一些当年的情谊和往事。你偷偷的问何子安什么时候给你东西，何子安悄悄的告诉你，只要你今晚听话，他就给你东西。<br><br>晚上7点多，何子安提议去他们这里有名的【last night 酒吧】喝一杯，顺便庆祝一下再次重相逢，大家都同意了。你虽然不想喝酒，但也不敢违逆何子安的意思。<br><br>8点，你们到了酒吧，一边喝酒一边聊天，不一会儿，一个带着墨镜，年轻漂亮的女孩子来到你们身边，她和何子安说了几句话，何子安介绍说这是他在大学里带的学生，名叫【安娜】，给大家认识一下。<br><br>接下来的事情你就记不得了……<br><br>你的任务:<br><br>1 隐瞒自己生活窘迫的事实（次要)<br><br>2寻找他人隐藏的秘密（次要)<br><br>3隐瞒你杀死何子安和安娜并抢走毒品的事情（重要)。<br><br>提示:尽量不要让别人知道你吸毒的事情。如果已经暴露，请尽量引导其他人不要将你吸毒的事情和何子安联系到一起。'
